@media (min-width: @mobile-width) {
.main-advantages__img{
  width: 60px;
  height: 60px;
  background-color: #68B738;
}
.svg-item2-4{
width: 60px;
height: 60px;
}
.svg-item1-3{
  width: 25px;
  height: 28px;
  padding: 32% 34%;
  }
.main-advantages__item{
  display: flex;
  gap: 20px;
  margin: 20px 0;
}
